//extras

//select2
.selecttwo-select-wrapper {
  position: relative;

  .select2-container--cbx {
    //width: 100% !important;
    //max-width: 100% !important;
    width: 100% !important;
    max-width: 500px;

    .selection {
      .select2-selection__clear {
        width: 26px;
        height: 24px;
        line-height: 24px;
        font-size: 16px;
        font-weight: normal;

        margin-top: 1px;
        text-align: center;
        border: 1px solid var(--color-primary);
        color: var(--color-primary);
        border-radius: 50%;
      }

      .select2-selection__arrow {
        width: 28px;
        display: inline-block;
      }
    }

    .select2-search--dropdown {
      .select2-search__field {
        min-height: auto !important;
        font-size: 12px !important;
        line-height: 1.2px !important;

      }
    }

    .select2-selection__rendered {
      .select2-selection__choice {
        font-size: 14px !important;
        line-height: 22px !important;
        padding: 3px 10px !important;
      }

      .select2-search {
        &.select2-search--inline {
          .select2-search__field {
            width: 100px !important;
            font-size: 12px !important;
            line-height: 22px !important;
            padding: 3px 5px !important;
            margin-top: 0 !important;
          }

        }
      }
    }

    .select2-selection--multiple {
      .select2-selection__rendered {
        display: flex;
        flex-wrap: wrap;

        .select2-selection__clear {
          //margin-left: auto !important;
          order: 99999;
          display: inline-block !important;
          margin: 2px 3px 2px 0 !important;
        }

        li {
          margin: 2px 3px 2px 0 !important;
          display: inline-block !important;
        }
      }
    }
  }
}


.cbx-chota {
  .select2-container {
    width: 100% !important;
  }

  .postbox {
    border-radius: 8px;
    border: 0 none;
    box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;

    .inside {
      margin-top: 0 !important;
      padding: 15px !important;
    }
  }
}

.cbx-listing-table-filters-wrap {
  margin-bottom: 10px;

  .col-12 {
    margin-bottom: 0;
  }

  .cbx-listing-table-filters {
    display: flex;
    flex-wrap: wrap;

    .cbx-listing-table-filter {
      margin-right: 10px;
      //max-width: 180px;
      //width: 100%;
      margin-bottom: 5px;

      input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
      select,
      textarea,
      textarea[type="text"] {

        width: 100% !important;
        display: inline-block !important;

        &.chota-inline {
          width: 100% !important;
          display: inline-block !important;
        }

        &.flatpickr-input-filter[type="text"] {
          width: auto !important;
        }

        &.select-filter[type="select"] {
          width: auto !important;
        }
      }

      &:last-child {
        margin-right: 0;
      }

      &.cbx-listing-table-filter-autow {
        width: auto !important;
        max-width: max-content !important;
      }

      &.cbx-listing-table-filter-chooser {
        max-width: 100% !important;
        width: auto !important;
      }

      &.cbx-listing-table-filter-date-picker {
        display: flex;
        flex-wrap: wrap;
        max-width: max-content !important;

        //max-width: 100% !important;
        //width: auto !important;

        .flatpickr-input[type=text] {
          //width: 150px !important;
          //width: auto !important;
          //
        }

        select {
          width: auto !important;
          //flex-grow: 1;
          //width: auto !important;
        }

        //max-width: 250px;
      }


      &.pull-right {
        margin-left: auto !important;
        float: none !important;
      }

      &.pull-left {
        margin-right: auto !important;
        float: none !important;
      }
    }
  }
}

.cbx-listing-table-prebox-wrap {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  //margin-bottom: 20px;

  .col {
    display: flex;
  }

  .col-12 {
    margin-bottom: 0;
  }

  .cbx-listing-table-prebox-l {
    display: flex;
    align-items: center;

    font-size: 14px !important;
    line-height: 2 !important;
    margin-bottom: 0 !important;

    h3{
      font-size: 20px !important;
      line-height: math.div(28, 20) !important;
      margin-bottom: 0 !important;
    }

  }

  .cbx-listing-table-prebox-r {
    display: flex;

    .form-control[type="text"], .form-control[type="textarea"] {
      font-size: 14px !important;
      line-height: 2 !important;
       padding-top: 0 !important;
      padding-bottom: 0 !important;

      //display: inline-block !important;
      //margin-right: 10px !important;
    }
  }

  .pull-right {
    margin-left: auto !important;
    float: none !important;
  }

  .pull-left {
    margin-right: auto !important;
    float: none !important;
  }
}



