
.cbx-backend_header_wrapper {
  .save-settings {
    img {
      width: 20px;
      height: 20px;
      margin-left: 5px;
      top: 5px;
      position: relative;
    }
  }

  #save_settings {
    margin-right: 10px;

    img {
      width: 20px;
      height: 20px;
      margin-right: 5px;
      top: 5px;
      position: relative;
    }
  }

  .menu-heading {
    justify-content: center;
    align-items: center;
    display: flex;

    img {
      margin-right: 5px;
    }
  }

  .setting_tool {
    justify-content: center;
    align-items: center;
    display: flex;
  }
}

@media only screen and (max-width: 960px) {
  .cbx-backend_container_header {
    left: 32px;
  }
}

@media screen and (max-width: 782px) {
  .cbx-backend_container_header {
    left: 0;
    top: 45px;

  }
}

@media screen and (max-width: 600px) {
  .cbx-backend_container_header {
    left: 0;
    top: 0;

  }
}

@media screen and (max-width: 545px) {
  .cbx-backend_header_wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    a {
      margin-top: 15px !important;
      display: flex !important;
      justify-content: center;
      float: none;
    }

    #save_settings {
      margin-right: 0 !important;
    }

    .menu-heading {
      flex-direction: column;
    }

    .setting_tool {
      flex-direction: column;
    }

  }
}

/*
Help & support Style Implemented
============================
*/
.cbx-backend-row {
  box-sizing: border-box;
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: wrap;
  margin-right: -.5rem;
  margin-left: -.5rem;
}

@media only screen and (max-width: 912px) {
  .cbx-backend-row {
    flex-direction: column;
  }

}

.cbx-backend-col.text-xs-left {
  text-align: left !important;
  justify-content: flex-start;
}

@media only screen and (min-width: 912px) {
  .cbx-backend-col.col-md-6 {
    flex-basis: 50%;
    max-width: 50%;
    box-sizing: border-box;
    padding: .5rem;
  }
}

.cbx-backend-col.col-md-12 {
  flex-basis: 100%;
  max-width: 100%;
  box-sizing: border-box;
  padding: .5rem;
  flex: 0 0 auto;
}

.cbx-backend-card {
  color: #141b38;
  background-color: #fff;
  box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05) !important;
  margin: 10px 0 20px;
  border-radius: 8px;
  font-size:14px;
  line-height: 1.2;

  .header {
    display: flex;
    align-items: center;
    height: 70px;
    padding: 0 30px;
    font-weight: 600;
    font-size: 18px;
    border-bottom: 1px solid #e8e8eb;

    .text {
      flex: 1 0 auto;
      display: flex;
      align-items: center;
    }
  }

  .content {
    padding: 30px;

    .dashboard-card-content {
      padding-top: 0;
      font-size: 16px;
      color: #141b38;
      text-align: left;

    }
  }
}


.dashboard-support {
  .cbx-backend-settings-row {
    margin-bottom: 25px;
    padding-bottom: 25px;
    border-bottom: 1px solid #e8e8eb;
  }

  .cbx-backend-settings-row:last-child {
    margin-bottom: 0;
    border-bottom: none;
    padding-bottom: 0;
  }

  .cbx-backend-settings-row a {
    display: flex;
    align-items: center;
    font-size: 16px;
    font-weight: 600;
    color: #141b38;
    text-decoration: none;
  }

  .cbx-backend-settings-row a:hover {
    color: var(--color-primary) !important;
  }

  .cbx-backend-settings-row svg {
    color: var(--color-primary);
    width: 24px;
    height: 24px;
    margin: -5px 10px 0 0;
  }
}

.cbx-backend-feature-card {
  height: 100%;
  background: #fff;
  box-shadow: 0 5px 18px 0 rgba(0, 4, 32, .10);
  color: #141b38;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  margin-bottom: 20px;

  .feature-card-body.static {
  }

  .feature-card-body {
    flex: 1;

    .feature-card-header {
      display: flex;
      align-items: center;
      font-size: 16px;
      font-weight: 600;
      margin-bottom: 16px;

      img {
        max-width: 100%;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;
      }
    }
  }

  .feature-card-description {
    padding: 0 10px;

    h3 {
      margin: 0;
      font-size: 14px;

      a {
        text-decoration: none;
      }
    }
  }
}

