/*
  with old method, spinner will shift when rotating if font-size contains floating point ( such as 22.5px ).
  here we use 2em in width and height, scaling back to 1em with transform: scale(.5) to prevent this effect.
*/
.ld-ball,
.ld-ring,
.ld-hourglass,
.ld-loader,
.ld-cross,
.ld-square,
.ld-pie,
.ld-spinner {
  width: 1em;
  height: 1em;
  position: relative;
  color: inherit;
  display: inline-block;
  box-sizing: content-box;
}
.ld-ball:after,
.ld-ring:after,
.ld-hourglass:after,
.ld-loader:after,
.ld-cross:after,
.ld-square:after,
.ld-pie:after,
.ld-spinner:after {
  content: " ";
  display: block;
  width: 2em;
  height: 2em;
  box-sizing: border-box;
  transform-origin: 0em 0em;
  transform: translateZ(0) scale(0.5);
  backface-visibility: hidden;
}
.ld-ring:after {
  border-radius: 50%;
  border: 0.3em solid currentColor;
  border-left-color: transparent;
}
.ld-ball:after {
  border-radius: 50%;
  background: currentColor;
}
.ld-hourglass:after {
  border-radius: 50%;
  border: 1em solid currentColor;
  border-left-color: transparent;
  border-right-color: transparent;
}
.ld-cross:after {
  position: absolute;
  height: 0.5em;
  transform: translateZ(0) scale(0.5) translate(0, 0.75em);
  background: currentColor;
}
.ld-cross:before {
  content: " ";
  display: block;
  width: 0.5em;
  height: 2em;
  box-sizing: border-box;
  transform-origin: 0em 0em;
  backface-visibility: hidden;
  position: absolute;
  width: 0.5em;
  transform: translateZ(0) scale(0.5) translate(0.75em, 0);
  background: currentColor;
}
.ld-square:after {
  background: currentColor;
}
.ld-pie:after {
  border-radius: 50%;
  border: 1em solid currentColor;
  border-left-color: transparent;
}
.ld-spinner:after {
  position: absolute;
  width: 0.4em;
  height: 0.4em;
  transform: translateZ(0) scale(0.5) translate(0.8em, 0.8em);
  border-radius: 50%;
  background: none;
