/*"0" => esc_html__( 'Unpublish', 'cbxwpsimpleaccounting' ),
"1" => esc_html__( 'Publish', 'cbxwpsimpleaccounting' ),
"2" => esc_html__( 'Suspended', 'cbxwpsimpleaccounting' ),
"3" => esc_html__( 'Expired', 'cbxwpsimpleaccounting' ),
"4" => esc_html__( 'Pending', 'cbxwpsimpleaccounting' ),
"5" => esc_html__( 'Draft', 'cbxwpsimpleaccounting' ),*/

.component_status{
  //background-color: var(--color-lightGrey);
  background-color: #e6e8ec;
  color: var( --color-darkGrey);
  padding: 3px 15px;
  font-size: 12px;
  line-height: 1.5;
  font-weight: 500;
  border-radius: 15px;
  border: 1px solid transparent;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
  justify-content: start;
  .dot{
    margin-right: 5px;
    width: 5px;
    height: 5px;
    border-radius: 50%;
    display: inline-block;
    background-color: var(--color-grey);
  }

  &:hover, &:focus{
    border: 1px solid var( --color-darkGrey);
  }

  &.component_status_icon{
    display: inline-flex;
    align-items: center;
    svg, img{
      width: 15px;
      height: 15px;
      margin-right: 5px;
    }

    svg{
      stroke-width: 1;
    }
  }

  &.component_status_unverified{
    background-color: #FFF1F3;
    color: #FF5E87;
    .dot{
      background-color: #FF5E87;
    }

    &:hover, &:focus{
      border: 1px solid #FFF1F3;
    }
  }

  &.component_status_pending, &.component_status_hold, &.component_status_4{
    background-color: var(--color-lightGrey);
    color: #5d6776;
    .dot{
      color: var(--color-darkGrey);
    }

    &:hover, &:focus{
      border: 1px solid var(--color-darkGrey);
    }
  }

  &.component_status_published, &.component_status_publish, &.component_status_approved, &.component_status_active, &.component_status_1{
    background-color: #ECFDF3;
    color: #027A48;
    .dot{
      background-color: #027A48;
    }

    &:hover, &:focus{
      border: 1px solid #027A48;
    }
  }

  &.component_status_unpublished, &.component_status_unapproved, &.component_status_inactive, &.component_status_0{
    background-color: #F0F9FF;
    color: #026AA2;
    .dot{
      background-color: #026AA2;
    }

    &:hover, &:focus{
      border: 1px solid #026AA2;
    }
  }

  &.component_status_expired, &.component_status_3{
    background-color: rgb(255 0 0 / 20%);
    color: var(--color-error);
    .dot{
      background-color:var(--color-error);
    }

    &:hover, &:focus{
      border: 1px solid var(--color-error);
    }
  }

  &.component_status_flag{
    background-color: #fde6d8;
    color: #ac5a2b;

    .dot{
      background-color: #ac5a2b;
    }

    &:hover, &:focus{
      border: 1px solid #ac5a2b;
    }
  }

  &.component_status_suspended, &.component_status_2{
    background-color: rgb(255 0 0 / 20%);
    color: var(--color-error);
    .dot{
      background-color: var(--color-error);
    }

    &:hover, &:focus{
      border: 1px solid var(--color-error);
    }
  }

  //resume privacy or any private/public
  &.component_status_public{
    background-color: #5d5dff2b;
    color: $app_primary_color;

    &:hover, &:focus{
      border: 1px solid var(--color-primary);
    }
  }

  &.component_status_private{
    background-color: rgb(255 0 0 / 20%);
    color: var(--color-error);
    &:hover, &:focus{
      border: 1px solid var(--color-error);
    }
  }
  //end resume privacy or any private/public
}