//https://iconsvg.xyz/
//for button
.icon-inline {
  display: inline-flex !important;
  align-items: center !important;
}

.icon{
  display: inline-flex !important;
  align-items: center !important;

  .cbx-icon{
    margin-right: 5px;
    order: 1;
  }

  .button-label{
    order: 2;
  }

  &.icon-right{
    .cbx-icon{
      margin-right: 0 !important;
      margin-left: 5px !important;
      order: 2;
    }

    .button-label{
      order: 1;
    }
  }

  &.icon-only{
    .cbx-icon{
      margin-right: 0;
      margin-left: 0;
    }
  }
}
//end for button

//for icons
.cbx-icon {
  background-size: contain !important;
  background-repeat: no-repeat !important;
  width: 20px;
  height: 20px;
  background-position: center;
  display: block;
  vertical-align: middle;
  background-color: transparent;
}

.cbx-icon-10 {
  width: 10px;
  height: 10px;
}


.cbx-icon-15 {
  width: 15px;
  height: 15px;
}

.cbx-icon-20 {
