.cbx-chota {
  details.dropdown {
    position: relative;
    display: inline-block;

     > :last-child {
      position: absolute;
      left: 0;
      white-space: nowrap;
    }


  }

  details.dropdown.dropdown-inline{
    display: block !important;

    > :last-child {
      position: static !important;
      left: auto;
      white-space: nowrap;
      width: 100%;
    }
  }
}




