@mixin reusable-btn-styles {
  text-decoration: none !important;
  height: 30px !important;
  min-height: 30px !important;
  padding: 0 8px !important;
  position: relative;
  background: var(--bg-color);
  color: var(--color-darkGrey);
  border-radius: 4px !important;
  border: 1px solid var(--color-primary) !important;
  text-align: center;
  transition: opacity 0.2s ease;
  transform: scale(1);
  display: inline-block;
  cursor: pointer;
  vertical-align: middle !important;
  font-weight: normal !important;
  font-size: 14px !important;
  line-height: 2 !important;
  box-sizing: border-box !important;
  white-space: nowrap !important;
  -webkit-appearance: none !important;
}

@mixin reusable-text-styles {
  font-family: inherit;
  //border-radius: 4px;
  //border: 1px solid var(--color-lightGrey);
  transition: all 0.2s ease;
  width: auto;
  display: inline-block;

  padding: 0 8px;
  font-size: 14px;
  line-height: 2;
  height: 30px;
  min-height: 30px;
  background-color: rgba(239, 242, 249, .7);
  //height: auto !important;
  box-sizing: border-box;
  border: 1px solid var(--color-primary) !important;
  border-radius: 4px !important;
}

.cbx-chota {
  .wp-header-end {
    margin: 5px 0;
  }

  .search-box {
    select {
      display: inline-block !important;
      width: auto !important;
    }

    input, textarea {
      display: inline-block !important;
      width: auto !important;
    }
  }

  #posts-filter{
    @media screen and (max-width: 767px) {
      .search-box{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
      }
    }
  }

  .tablenav {
    select {
      display: inline-block !important;
      width: auto !important;
      margin-bottom: 0;
    }

    &.top {
      @media screen and (max-width: 782px) {
        margin-top: 0;
        margin-bottom: 10px;
      }

      .actions {
        @media screen and (max-width: 782px) {
          display: flex;
          justify-content: center;
          flex: 0 0 100% !important;
          width: 100% !important;
          margin-bottom: 10px;
        }
      }

      .tablenav-pages {
        display: flex;
        align-items: center;
        flex: 0 0 100% !important;
        width: 100% !important;
        justify-content: center;

        &.one-page {
          //display: block;
        }
      }
    }

    &.bottom {
      .actions {

      }

      @media screen and (max-width: 782px) {
        display: flex;
        flex-wrap: wrap;
        .actions {
          display: flex;
          align-items: center;
          flex: 0 0 100% !important;
          width: 100% !important;
          justify-content: center;

          select {
            margin-bottom: 0 !important;
          }
        }
        .tablenav-pages {
          display: flex;
          align-items: center;
          flex: 0 0 100% !important;
          width: 100% !important;
          justify-content: center;

          &.one-page {
            display: flex;
            align-items: center;
            flex: 0 0 100% !important;
            width: 100% !important;
            justify-content: center;

            .displaying-num {
              position: initial;
            }
          }
        }
      }
    }
  }

  #current-page-selector {
    display: inline-block !important;
    width: auto !important;
  }

  .toggle-row {
    width: auto !important;
    height: auto !important;
    display: none;
    top: 2px;
    //right: 30px;
    border-radius: 50% !important;
    padding: 0 !important;
    background: none !important;
    //margin-left: 30px !important;

    &:before {
      border-radius: 50% !important;
      padding: 2px !important;
      top: 2px;
      left: 0;
      box-shadow: 0 0 0 1px #CCCCCC, 0 0 2px 1px rgb(79, 148, 212);
    }

    &:focus {
      &:before {
        box-shadow: 0 0 0 1px #4f94d4, 0 0 2px 1px rgb(79, 148, 212);
      }
    }
  }

  #search-submit {
    background-color: var(--color-primary);
    color: #fff;

    &:hover, &:focus {
      background-color: var(--color-primary);
      color: #fff;
    }
  }

  #addtag {
    display: none;
  }

  #addtag, #edittag {
    select {
      width: 100%;
    }
  }

  #ajax-response {

  }

  .wp-list-table {
    .inline {
      .button-link {
        background-color: transparent !important;
        padding: 0 !important;
      }
    }
  }

  .handle-order-higher, .handle-order-lower, .handlediv {
    width: auto;
    height: auto;
    margin-right: 5px;

    &:last-child {
      margin-right: 0;
    }
  }

  .cbx-wplisttable {
    .widefat tfoot tr td,
    .widefat tfoot tr th,
    .widefat thead tr td,
    .widefat thead tr th {
      padding: 5px !important;

      a {
        padding: 0 !important;
      }
    }
  }

  .wp-list-table tr:not(.inline-edit-row):not(.no-items) td.column-primary ~ td:not(.check-column) {
    @media screen and (max-width: 782px) {
      padding: 3px 8px 3px 35% !important;
    }

  }

}

