.cbx-page-wrapper {
  margin-top: 50px;

  .container {
    max-width: 1200px;
  }
}
.wp-heading-wrap {
  display: flex;
  flex-wrap: wrap;

  margin: 0;

  &.wp-heading-wrap-edit{
    //margin: 0 10px;
  }

  #setting-error-settings_updated{
    width: 100%;
    p{
      margin-bottom: 0;
    }

    button{
      background: transparent !important;
      border: none !important;
    }
  }

  @media screen and (max-width: 600px) {
    justify-content: center;
  }

  .wp-heading-inline {
    padding: 0 !important;
    margin: 0 !important;
    line-height: 1 !important;
    margin-right: auto;

    @media screen and (max-width: 600px) {
      font-size: 25px !important;
      text-align: center;
    }
  }

  .pull-left {
    order: 1;
    margin-right: auto;

    @media screen and (max-width: 600px) {
      display: flex;
      flex-wrap: wrap;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;

      margin-bottom: 20px;
    }
  }

  .pull-right {
    order: 2;
    margin-left: auto;
    max-height: 28px;

    @media screen and (max-width: 600px) {
      display: flex;
      flex-wrap: wrap;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
    }
  }

  .wp-heading-wrap-left{}
  .wp-heading-wrap-right{
    display: flex;
    flex-wrap: wrap;
  }
}