/*
  with old method, spinner will shift when rotating if font-size contains floating point ( such as 22.5px ).
  here we use 2em in width and height, scaling back to 1em with transform: scale(.5) to prevent this effect.
*/
.ld-ball,
.ld-ring,
.ld-hourglass,
.ld-loader,
.ld-cross,
.ld-square,
.ld-pie,
.ld-spinner {
  width: 1em;
  height: 1em;
  position: relative;
  color: inherit;
  display: inline-block;
  box-sizing: content-box;
}

.ld-ball:after,
.ld-ring:after,
.ld-hourglass:after,
.ld-loader:after,
.ld-cross:after,
.ld-square:after,
.ld-pie:after,
.ld-spinner:after {
  content: " ";
  display: block;
  width: 2em;
  height: 2em;
  box-sizing: border-box;
  transform-origin: 0em 0em;
  transform: translateZ(0) scale(0.5);
  backface-visibility: hidden;
}

.ld-ring:after {
  border-radius: 50%;
  border: 0.3em solid currentColor;
  border-left-color: transparent;
}

.ld-ball:after {
  border-radius: 50%;
  background: currentColor;
}

