.ld-ext-right,
.ld-ext-left,
.ld-ext-bottom,
.ld-ext-top,
.ld-over,
.ld-over-inverse,
.ld-over-full,
.ld-over-full-inverse {
  position: relative;
}

.ld-ext-right > .ld,
.ld-ext-left > .ld,
.ld-ext-bottom > .ld,
.ld-ext-top > .ld,
.ld-over > .ld,
.ld-over-inverse > .ld,
.ld-over-full > .ld,
.ld-over-full-inverse > .ld {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 1em;
  height: 1em;
  margin: -0.5em;
  opacity: 0;
  z-index: -1;
  transition: all 0.3s;
  transition-timing-function: ease-in;
  animation-play-state: paused;
}

.ld-ext-right > .ld > *,
.ld-ext-left > .ld > *,
.ld-ext-bottom > .ld > *,
.ld-ext-top > .ld > *,
.ld-over > .ld > *,
.ld-over-inverse > .ld > *,
.ld-over-full > .ld > *,
.ld-over-full-inverse > .ld > * {
  width: 1em;
  height: 1em;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-0.5em, -0.5em);
}

.ld-ext-right.running > .ld,
.ld-ext-left.running > .ld,
.ld-ext-bottom.running > .ld,
.ld-ext-top.running > .ld,
.ld-over.running > .ld,
.ld-over-inverse.running > .ld,
.ld-over-full.running > .ld,
.ld-over-full-inverse.running > .ld {
  opacity: 1;
  z-index: auto;
  visibility: visible;
  animation-play-state: running !important;
}

