table.widefat {
  border-top: 1px solid var(--color-primary);
  border-bottom-right-radius: 8px;
  border-bottom-left-radius: 8px;
  box-shadow: 0 10px 22px 0 rgba(0, 4, 32, .05);
  border-bottom: none;
  border-left: none;
  border-right: none;
}


.cbxuseronline-page-wrapper {
  margin-top: 50px;

  .container {
    max-width: 1200px;
  }
}

