.wp-heading-wrap {
  display: flex;
  flex-wrap: wrap;

  #setting-error-settings_updated{
    width: 100%;
    p{
      margin-bottom: 0;
    }

    button{
      background: transparent !important;
      border: none !important;
    }
  }

  @media screen and (max-width: 600px) {
    justify-content: center;
  }

  .wp-heading-inline {
    padding: 0 !important;
    margin: 0 !important;
    line-height: 1 !important;
    margin-right: auto;

    @media screen and (max-width: 600px) {
      font-size: 25px !important;
      text-align: center;
    }
  }

  .pull-left {
    order: 1;
    margin-right: auto;

    @media screen and (max-width: 600px) {
      display: flex;
      flex-wrap: wrap;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;

      margin-bottom: 20px;
    }
  }

  .pull-right {
    order: 2;
    margin-left: auto;
    max-height: 28px;

    @media screen and (max-width: 600px) {
      display: flex;
      flex-wrap: wrap;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
    }
  }
}

.cbxuseronline_wplisting_filters_wrap, .cbx-wplisttable-before-wrapper {
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;

  .left-filter-panel{
    margin-right: auto !important;
    display: flex;
    flex-wrap: wrap;

    @media screen and (max-width: 782px) {
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
      height: auto !important;
      margin-bottom: 20px;
    }
  }
  .right-filter-panel{
    margin-left: auto !important;
    display: flex;
    flex-wrap: wrap;

    @media screen and (max-width: 782px) {
      margin-bottom: 10px;
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
    }
  }

  .pull-right, .search-box {
    margin-left: auto !important;

    @media screen and (max-width: 782px) {
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
      height: auto !important;
      margin-bottom: 0;
    }
  }

  .pull-left {
    margin-right: auto !important;
    @media screen and (max-width: 782px) {
      margin-bottom: 10px;
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
    }
  }


}

.subsubsub {
  list-style: none;
  margin: 8px 0 0;
  padding: 0;
  font-size: 13px;
  float: left;
  color: #646970;

  li {
    display: inline-block;
    margin: 0;
    padding: 0;
    white-space: nowrap;

    a {
      line-height: 2;
      padding: .2em;
      text-decoration: none;

      &.current {
        font-weight: 600;
        border: none;
      }
    }
  }
}

