@use "sass:math";

.cbx-chota {
  /*@keyframes btnspinner {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }*/

  fieldset {
    //padding: 0.5rem 2rem;
  }

  legend {
    text-transform: uppercase;
    font-size: 0.8em;
    letter-spacing: 0.1rem;
  }

  input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
  select,
  textarea,
  textarea[type="text"] {
    font-family: inherit;
    border-radius: 4px;
    border: 1px solid var(--color-lightGrey);
    transition: all 0.2s ease;
    display: block;
    width: auto;

    &.chota-inline{
      width: auto;
      display: inline-block;
    }
  }

  //handle modal fields
  .modal__content, .cbx_form_wrapper{
    input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
    select,
    textarea,
    textarea[type="text"] {
      width: 100%;
    }

    &.chota-inline{
      width: auto;
      display: inline-block;
    }
  }

  .global_setting_group{
    input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
    select,
    textarea,
    textarea[type="text"]{
      width: 100%;
      max-width: 500px;
    }
    .wpsa-browse-wrap{
      width: 100%;
      max-width: 500px;
    }

    form{
      .form-table td[colspan="2"]{
        padding: 15px 0;
      }
    }
  }

  input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]):not(:disabled):hover,
  select:hover,
  textarea:hover,
  textarea[type="text"]:hover {
    border-color: var(--color-grey);
  }

  input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]):focus,
  select:focus,
  textarea:focus,
  textarea[type="text"]:focus {
    outline: none;
    border-color: var(--color-primary);
    box-shadow: 0 0 1px var(--color-primary);
  }

  input.error:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
  textarea.error {
    border-color: var(--color-error);
  }

  input.success:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]),
  textarea.success {
    border-color: var(--color-success);
  }

  input[type="text"], input[type="password"], input[type="color"], input[type="date"], input[type="datetime"], input[type="datetime-local"], input[type="email"], input[type="month"], input[type="number"], input[type="search"], input[type="tel"], input[type="time"], input[type="url"], input[type="week"], select, textarea{
    padding: 0 8px;
    font-size: 14px;
    line-height: 2;
    height: 30px;
    min-height: 30px;
    background-color: rgba(239, 242, 249, .7);
    //height: auto !important;
    box-sizing: border-box;
  }
  textarea{
    height: auto;
    box-sizing: border-box;
  }

  select {
    -webkit-appearance: none;
    //background: #f3f3f6 no-repeat 100%;
    //background: var(--bg-color);
    background-color: rgba(239, 242, 249, .7);
    background-size: 1ex !important;
    background-origin: content-box !important;
    background-image: url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='60' height='40' fill='%23555'><polygon points='0,0 60,0 30,40'/></svg>") !important;
    background-repeat: no-repeat !important;
    //background-position-x: right 5px;
    //background-position-y: top center;
    background-position: right -10px center !important;
    padding: 0 18px 0 10px !important;
    font-size: 14px !important;
    line-height: 2 !important;
    height: auto !important;
    box-sizing: border-box;
  }

  input:not(.magic-checkbox)[type="checkbox"],
  input:not(.magic-radio)[type="radio"] {
    width: 20px !important;
    height: 20px !important;
  }

  input:not(.magic-checkbox)[type="checkbox"]:checked::before, input:not(.magic-radio)[type="radio"]:checked::before{
    width: 20px !important;
    height: 20px !important;
    margin: 0 !important;
  }

  .button,
  summary.button,
  [type="button"],
  [type="reset"],
  [type="submit"],
  button:not(.handle-order-higher):not(.handle-order-lower):not(.toggle-row), #wp-submit {
    text-decoration: none !important;
    height: 30px !important;
    min-height: 30px !important;
    padding:0 8px !important;
    position: relative;
    background: var(--bg-color);
    color: var(--color-darkGrey);
    border-radius: 4px !important;
    border: 1px solid transparent !important;
    text-align: center;
    transition: opacity 0.2s ease;
    transform: scale(1);
    display: inline-block;
    cursor: pointer;
    vertical-align: middle !important;
    font-weight: normal !important;
    font-size: 14px !important;
    line-height: 2 !important;
    box-sizing: border-box !important;
    white-space: nowrap !important;
    -webkit-appearance: none !important;

    &:hover, &:focus{
      border: 1px solid transparent;
      box-shadow: none !important;
      outline: none !important;
    }

    .dashicons {
      vertical-align: middle;
    }

    .cbx-icon{
      width: 20px;
      height: 20px;
    }

    &.small{
      font-size: 13px !important;
      line-height: math.div(24, 13) !important;
      min-height: 24px !important;
      padding: 0 6px !important;

      .cbx-icon{
        width: 16px !important;
        height: 16px !important;
      }
    }

    &.icon{
      display: inline-flex !important;
      align-items: center !important;
    }

    &.icon-only {
      //display: inline-flex !important;
      //align-items: center !important;

      line-height: 1 !important;
      padding: 4px 8px !important;
      text-align: center;

      &.small{
        padding: 4px 8px !important;

        .cbx-icon{
          width: 16px;
          height: 16px;
        }
      }
    }




    /*&.button-spin {
      padding-left: 30px !important;
      display: block !important;

      .spinner {
        left: -6px !important;
        top: 6px !important;
        width: 28px !important;
        display: block;
        position: absolute;

        &:before{
          content: "";
          width: 14px !important; !* Size of the spinner *!
          height: 14px !important; !* Change as desired *!
          position: absolute;
          top: 50%;
          left: 50%;
          border-radius: 50%;
          border: 2px solid #999; !* Thickness/color of spinner track *!
          border-bottom-color: #555; !* Color of variant spinner piece *!
          animation: .8s linear infinite btnspinner; !* speed of spinner *!
          transform: translate(-50%, -50%);
          will-change: transform;
        }
      }
    }*/
  }

  .grouped {
    display: flex;
  }

  .grouped > *:not(:last-child) {
    margin-right: 16px;
  }

  .grouped.gapless > * {
    margin: 0 0 0 -1px !important;
    border-radius: 0 !important;
  }

  .grouped.gapless > *:first-child {
    margin: 0 !important;
    border-radius: 4px 0 0 4px !important;
  }

  .grouped.gapless > *:last-child {
    border-radius: 0 4px 4px 0 !important;
  }

  .button + .button {
    margin-left: 10px;
  }

  .button:hover,
  [type="button"]:hover,
  [type="reset"]:hover,
  [type="submit"]:hover,
  button:hover {
    opacity: 0.8;
  }

  .button:active,
  [type="button"]:active,
  [type="reset"]:active,
  [type="submit"]:active,
  button:active {
    transform: scale(0.98);
  }

  input:disabled,
  button:disabled,
  input:disabled:hover,
  button:disabled:hover {
    opacity: 0.4;
    cursor: not-allowed;
  }

  .button.primary,
  .button.secondary,
  .button.dark,
  .button.light,
  .button.error,
  .button.success,
  [type="submit"] {
    color: var(--font-color);
    z-index: 1;
    background-color: transparent;
    //background-color: var(--color-primary);
  }

  .button.primary{
    background-color: var(--color-primary);
    //background: linear-gradient(94.37deg, #653FFF 0.47%, #883FFF 90.73%);
    color: #fff;

    &:hover, &:focus{
      background-color: var(--color-primary);
      color: #fff;
    }
  }

  .button.secondary {
    background-color: var(--color-secondary) !important;
    color: #fff !important;

    &:hover, &:focus{
      background-color: var(--color-secondary) !important;
      color: #fff !important;
    }
  }

  .button.dark {
    background-color: var(--color-darkGrey) !important;
  }

  .button.light {
    background-color: var(--color-lightGrey) !important;
  }

  .button.error {
    background-color: var(--color-error) !important;
    color: #fff !important;
  }

  .button.outline.error {
      color: var(--color-error) !important;
      background-color: var(--color-lightGrey) !important;
  }

  .button.success {
    background-color: var(--color-success) !important;
    color: #fff !important;
  }

  .button.outline {
    background-color: transparent !important;
    border-color: var(--color-lightGrey) !important;
  }

  .button.outline.primary {
    border-color: var(--color-primary) !important;
    color: var(--color-primary) !important;
    background-color: transparent !important;
  }

  .button.outline.secondary {
    border-color: var(--color-grey) !important;
    color: var(--color-grey) !important;

    &:hover, &:focus{
      background-color: transparent !important;

    }
  }

  .button.outline.dark {
    border-color: var(--color-darkGrey) !important;
    color: var(--color-darkGrey) !important;
  }

  .button.outline.light {
    border-color: var(--color-lightGrey) !important;
    color: var(--color-lightGrey) !important;
  }

  .button.clear {
    background-color: transparent;
    border-color: transparent;
    color: var(--color-primary)  !important;
  }



  /*.button.icon > img {
    margin-left: 2px;
  }*/

  /*.button.icon-only {
    //display: inline-flex;
    //align-items: center;
    line-height: 1 !important;
    padding: 4px 8px !important;
    text-align: center;

    &.small{
      padding: 4px 8px !important;

      .cbx-icon{
        width: 16px;
        height: 16px;
      }
    }
  }*/

  ::placeholder {
    color: #bdbfc4;
  }


  input.vtl-tbody-checkbox[type="checkbox"], input.vtl-thead-checkbox[type="checkbox"]{
    width: 26px !important;
    height: 26px !important;
    border: 1px solid var(--color-grey) !important;
    outline: 0 none !important;
    padding: 0 !important;
    position: relative !important;
    margin: 0 !important;
    -webkit-appearance: none !important;
    background-color: #ffffff;
    border-radius: 4px !important;

    &:focus, &:hover{
      border: 1px solid var(--color-darkGrey) !important;
      outline: 0 none !important;
      box-shadow: none !important;
    }
    &:before{
      position: absolute !important;
      top: 2px !important;
      left: 2px !important;
      display: block !important;
      width: 20px !important;
      height: 20px !important;
      content: '' !important;
      background-image: url("../icons/icon_unchecked.svg") !important;
      background-size: contain !important;
      background-position: center !important;
      background-repeat: no-repeat !important;
    }
    &:checked{
      &:before{
        display: inline-block;
        content: '' !important;
        background-image: url("../icons/icon_checked.svg") !important;
        background-size: contain !important;
        background-position: center !important;
        background-repeat: no-repeat !important;
      }
    }
  }
}

