.cbx_wplisting_filters_wrap {
  width: 100%;
  margin-top: 10px;
  display: flex;
  flex-wrap: wrap;

  .pull-right, .search-box {
    margin-left: auto !important;

    @media screen and (max-width: 782px) {
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
      height: auto !important;
      margin-bottom: 0;
    }
  }

  .pull-left {
    margin-right: auto !important;
    @media screen and (max-width: 782px) {
      margin-bottom: 10px;
      display: flex;
      position: initial !important;
      flex: 0 0 100% !important;
      width: 100% !important;
      justify-content: center;
    }
  }


}

.subsubsub {
  list-style: none;
  margin: 8px 0 0;
  padding: 0;
  font-size: 13px;
  float: left;
  color: #646970;

  li {
    display: inline-block;
    margin: 0;
    padding: 0;
    white-space: nowrap;

    a {
      line-height: 2;
      padding: .2em;
      text-decoration: none;

      &.current {
        font-weight: 600;
        border: none;
      }
    }
  }

  @media screen and (max-width: 767px) {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-bottom: 10px;
  }
}

.postbox-header {
  h2 {
    padding: 5px 12px !important;
    font-size: 16px !important;
    line-height: 1.2 !important;
    margin-bottom: 0 !important;
  }
}